@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wdth,wght@63.9,200..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
    --background-color: rgb(14, 14, 14);
    --nav-background: rgb(18, 18, 18);
    --hover-color: aqua;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

::-webkit-scrollbar {
    display: none;
}

* {
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}

/* Index style start */
body {
    font-family: Arial, sans-serif;
    background-color: rgb(14, 14, 14);
    color: white;
}

nav {
    height: 80px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: var(--nav-background);
    backdrop-filter: blur(50px);
    box-shadow: 0 4px 15px rgba(248, 248, 248, 0.168);
    border-bottom: 1px solid rgba(153, 142, 142, 0.712);
    padding: 0 40px;
}

nav ul {
    display: flex;
    justify-content: center;
}

.left {
    font-size: 1.5rem;
    margin-left: -547px;
    /* Added margin to the left section */
}

.right {
    margin-right: -555px;
    /* Added margin to the right section */
}

.left a {
    text-decoration: none;
    color: white;
    transition: background 0.6s, color 0.6s, border 0.6s;
}

.left a:hover,
nav ul li a:hover {
    color: var(--hover-color);
    transform: scale(1.05);
}

nav ul li {
    list-style: none;
    margin: 0 23px;
    /* Adjusted margin between menu items */
}

nav ul li a {
    text-decoration: none;
    color: white;
    transition: color 0.4s ease;
}

.left a:focus {
    outline: 2px dashed var(--hover-color);
}

/* Main section style start */
.firstSection {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 80px 0;
}

.firstSection>div {
    width: 30%;
    min-width: 250px;
    box-sizing: border-box;
}

.leftSection {
    font-size: 3rem;
}

.leftSection .button {
    padding: 23px;
}

.leftSection .btn {
    padding: 12px;
    background: rgb(18, 18, 18);
    color: white;
    border: 2px solid white;
    border-radius: 12px;
    font-size: 20px;
    cursor: pointer;
    transition: background 0.6s, color 0.6s, border 0.6s;
    text-align: center;
    margin-right: 10px;
}

.leftSection .btn a {
    text-decoration: none;
    color: white;
    display: block;
}

.leftSection .btn:hover {
    background: white;
    color: rgb(18, 18, 18);
    border: 2px solid rgb(18, 18, 18);
}

.leftSection .btn:hover a {
    color: rgb(18, 18, 18);
    transform: scale(1.05);
}

.rightSection img {
    width: 80%;
    max-width: 400px;
    height: auto;
}

.purple {
    color: rgb(110, 234, 247);
}

#element {
    color: aqua;
}

main hr {
    border: 0;
    background: rgb(53, 54, 54);
    height: 1.2px;
    margin: 40px 84px;
}

/* Main section style ends */

/* Footer style start */
footer {
    background-color: rgb(18, 18, 18);
    backdrop-filter: blur(50px);
    box-shadow: 0 4px 15px rgba(248, 248, 248, 0.168);
    border-bottom: 1px solid rgba(153, 142, 142, 0.712);
    height: auto;
}

.footer {
    display: flex;
    justify-content: space-evenly;
    padding: 23px 122px;
    flex-wrap: wrap;
    /* Responsive for smaller screens */
    margin-top: 300px;
}

.footer>div {
    width: 223px;
    text-align: center;
    /* Center align for better appearance */
    margin-bottom: 20px;
    /* Margin for spacing between sections */
}

.footer p {
    font-size: 14px;
    /* Font size for the copyright text */
    color: #6c757d;
    /* Gray color for the text */
    margin: 0;
    /* Remove default margin */
}

.footer ul {
    list-style: none;
    padding: 0;
}

.footer ul li {
    margin: 10px 0;
    transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
    /* Smooth hover effect */
}

.footer ul li:hover {
    color: aqua;
    transform: scale(1.1);
    /* Slight zoom on hover */
}

.footer ul li a {
    color: white;
    text-decoration: none;
    transition: color 0.4s ease-in-out;
}

.footer ul li a:hover {
    color: aqua;
}

.footer-rights {
    text-align: center;
    color: gray;
    font-size: 0.9rem;
    padding: 30px 0;
    background-color: rgb(28, 28, 28);
    /* Darker background for separation */
}

/* Footer style ends */

/* About me style start */
/* Hero Section */
.about-hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 80px 0;
    padding: 0 50px;
    background: linear-gradient(135deg, rgb(18, 18, 18), rgb(24, 24, 24));
}

.hero-text h1 {
    font-size: 3rem;
    color: white;
}

.hero-text p {
    font-size: 1.2rem;
    color: gray;
    margin: 20px 0;
}

.hero-image img {
    width: 60%;
    border-radius: 15px;
}

/* Biography Section */
.biography {
    background: linear-gradient(135deg, rgb(18, 18, 18), rgb(24, 24, 24));
    padding: 60px 50px;
    color: white;
    text-align: center;
}

.biography h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
}

.biography p {
    font-size: 1.2rem;
    margin-bottom: 20px;
}

/* Skills Section */
.skills {
    padding: 50px;
    background: linear-gradient(135deg, rgb(24, 24, 24), rgb(36, 36, 36));
}

.skills h2 {
    font-size: 2.5rem;
    color: white;
    text-align: center;
    margin-bottom: 40px;
}

.skills-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 20px auto;
    max-width: 1200px;
    gap: 20px;
}

.skill-item {
    background: linear-gradient(145deg, rgba(255, 255, 255, 0.1), rgba(200, 200, 200, 0.1));
    border-radius: 10px;
    padding: 20px;
    text-align: center;
    transition: transform 0.3s, box-shadow 0.3s;
    width: 200px;
    /* Consistent width for skill items */
}

.skill-item:hover {
    transform: translateY(-5px);
    /* Slight lift on hover */
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.663);
}

.skill-item h3 {
    margin: 10px 0;
    color: white;
}

/* Skill Card Style */
.skill-card {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 20px;
    text-align: center;
    width: 200px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
    /* Change cursor to pointer */
}

.skill-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.skill-card h3 {
    margin: 0;
    font-size: 1.5em;
    color: white;
    transition: color 0.3s;
}

.skill-card:hover h3 {
    color: black;
}

/* Skill Section end */

/* Project style start */

.projects-section {
    padding: 50px;
    text-align: center;
}

.projects-section h1 {
    font-size: 3rem;
    margin-bottom: 40px;
}

.projects-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}

.project-box {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    padding: 20px;
    width: 250px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    cursor: pointer;
    /* Change cursor to pointer */
}

.project-box:hover {
    transform: translateY(-8px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.project-box h3 {
    font-size: 1.5em;
    color: white;
    transition: color 0.3s;
}

.project-box:hover h3 {
    color: black;
}

.project-box a {
    color: aqua;
    text-decoration: none;
}

/* Add a responsive adjustment */
@media (min-width: 600px) {
    .project-container {
        flex-direction: row;
        /* Change to row direction for larger screens */
        flex-wrap: wrap;
        /* Allow wrapping of project boxes */
    }

    .project-row {
        flex-direction: row;
        /* Align items in a row */
        justify-content: center;
        /* Center items in this row */
    }
}

/* Project style end */

/* Contact style start */
.contact-section {
    padding: 30px;
    /* Reduced padding */
    background-color: rgba(18, 18, 18, 0.8);
    /* Transparent background */
    border-radius: 10px;
    /* Rounded corners */
    max-width: 400px;
    /* Set maximum width */
    margin: 20px auto;
    /* Added margin for spacing from other elements */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    /* Box shadow for depth */
}

.contact-section h1 {
    font-size: 2rem;
    /* Smaller font size */
    color: white;
    text-align: center;
    margin-bottom: 15px;
    /* Increased margin */
}

.contact-section p {
    text-align: center;
    font-size: 1rem;
    /* Smaller font size */
    color: gray;
    margin-bottom: 20px;
    /* Increased margin */
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-form input,
.contact-form textarea {
    width: 90%;
    /* Full width within the box */
    padding: 10px;
    margin-bottom: 15px;
    /* Reduced margin */
    border: none;
    border-radius: 6px;
    background: rgba(255, 255, 255, 0.1);
    /* Transparent input background */
    color: white;
    transition: background 0.3s;
}

.contact-form input:focus,
.contact-form textarea:focus {
    background: rgba(255, 255, 255, 0.2);
    /* Lighter on focus */
}

.contact-form button {
    padding: 10px 20px;
    /* Reduced padding */
    border: none;
    border-radius: 6px;
    background-color: aqua;
    color: black;
    font-size: 0.9rem;
    /* Smaller font size */
    cursor: pointer;
    transition: background 0.3s;
    margin-top: 10px;
    /* Added margin to the button */
}

.contact-form button:hover {
    background-color: rgb(18, 18, 18);
    color: aqua;
}

/* Contact style end */

/* Media Queries for Responsive Design */

/* Extra Large Devices (Desktops) */
@media (max-width: 1200px) {
    nav {
        padding: 0 20px;
        /* Adjust padding for medium screens */
    }

    .firstSection>div {
        width: 40%;
        /* Adjust width for medium screens */
    }

    .left {
        font-size: 1.3rem;
        /* Slightly smaller font size */
        margin-left: 0;
        /* Reset margin for alignment */
    }

    .right {
        margin-right: 0;
        /* Reset margin for alignment */
    }
}

/* Medium Devices (Tablets) */
@media (max-width: 992px) {
    nav {
        flex-direction: column;
        /* Stack navigation items */
        padding: 10px;
        /* Adjust padding */
    }

    .firstSection {
        flex-direction: column;
        /* Stack sections vertically */
        align-items: center;
        /* Center align items */
    }

    .firstSection>div {
        width: 80%;
        /* Full width on smaller screens */
        margin-bottom: 20px;
        /* Add space between sections */
    }

    .leftSection {
        font-size: 2.5rem;
        /* Decrease font size */
    }

    .rightSection img {
        width: 100%;
        /* Full width for images */
    }
}

/* Small Devices (Mobile Phones) */
@media (max-width: 768px) {
    .firstSection {
        margin: 40px 0;
        /* Decrease margin for smaller screens */
    }

    .firstSection>div {
        width: 90%;
        /* Take almost full width for project boxes */
    }

    .leftSection {
        font-size: 2rem;
        /* Smaller font size */
    }

    .leftSection .btn {
        font-size: 18px;
        /* Smaller button font size */
    }

    .rightSection img {
        max-width: 90%;
        /* Adjust image size */
    }
}

/* Extra Small Devices (Phones in Portrait Mode) */
@media (max-width: 576px) {
    nav {
        padding: 5px;
        /* Less padding for mobile */
    }

    .firstSection>div {
        width: 95%;
        /* Almost full width for boxes */
        margin: 0 auto;
        /* Center align boxes */
    }

    .leftSection {
        font-size: 1.5rem;
        /* Smaller font size */
    }

    .leftSection .btn {
        font-size: 16px;
        /* Smaller button font size */
    }

    main hr {
        margin: 20px 0;
        /* Decrease margin for mobile */
    }
}

/* Large Devices (Desktops) */
@media (max-width: 1200px) {
    .footer {
        padding: 23px 80px;
        /* Less padding on medium screens */
    }

    .footer>div {
        width: 200px;
        /* Slightly decrease width for medium screens */
    }
}

/* Medium Devices (Tablets) */
@media (max-width: 992px) {
    .footer {
        padding: 20px 40px;
        /* Adjust padding */
        justify-content: center;
        /* Center footer content */
    }

    .footer>div {
        width: 45%;
        /* Adjust width for better fit */
        margin-bottom: 20px;
        /* Margin for spacing */
    }
}

/* Small Devices (Mobile Phones) */
@media (max-width: 768px) {
    .footer {
        padding: 15px 20px;
        /* Less padding for mobile */
    }

    .footer>div {
        width: 90%;
        /* Full width for sections */
        margin: 0 auto;
        /* Center align sections */
    }

    .footer-rights {
        font-size: 0.8rem;
        /* Smaller font size */
    }
}

/* Extra Small Devices (Phones in Portrait Mode) */
@media (max-width: 576px) {
    .footer {
        flex-direction: column;
        /* Stack footer content vertically */
        align-items: center;
        /* Center align items */
    }

    .footer>div {
        width: 100%;
        /* Full width for all sections */
        margin-bottom: 10px;
        /* Smaller margin */
    }
}

/* Large Devices (Desktops) */
@media (max-width: 1200px) {
    .about-hero {
        padding: 0 30px;
        /* Less padding on medium screens */
        flex-direction: column;
        /* Stack hero content vertically */
        align-items: center;
        /* Center align items */
    }

    .hero-image img {
        width: 80%;
        /* Wider image for medium screens */
    }

    .biography,
    .skills {
        padding: 40px 30px;
        /* Adjust padding */
    }

    .skills h2 {
        font-size: 2rem;
        /* Smaller heading for skills */
    }
}

/* Medium Devices (Tablets) */
@media (max-width: 992px) {
    .about-hero {
        margin: 40px 0;
        /* Adjust margin */
    }

    .hero-text h1 {
        font-size: 2.5rem;
        /* Adjust heading size */
    }

    .hero-text p {
        font-size: 1rem;
        /* Smaller paragraph text */
    }

    .skills-container {
        justify-content: center;
        /* Center for better fit */
    }

    .skill-item {
        width: 45%;
        /* Adjust width for better fit */
    }
}

/* Small Devices (Mobile Phones) */
@media (max-width: 768px) {
    .about-hero {
        flex-direction: column;
        /* Stack elements */
        align-items: center;
        /* Center align */
        padding: 20px 15px;
        /* Less padding */
    }

    .hero-text h1 {
        font-size: 2rem;
        /* Smaller heading size */
    }

    .hero-image img {
        width: 100%;
        /* Full width on mobile */
        border-radius: 10px;
        /* Adjust border radius */
    }

    .biography,
    .skills {
        padding: 30px 15px;
        /* Less padding for mobile */
    }

    .skill-item {
        width: 90%;
        /* Full width for skill items */
    }
}

/* Extra Small Devices (Phones in Portrait Mode) */
@media (max-width: 576px) {

    .biography h2,
    .skills h2 {
        font-size: 2rem;
        /* Smaller heading for smaller screens */
    }

    .biography p,
    .skills-container {
        font-size: 1rem;
        /* Smaller text */
    }

    .skill-card {
        width: 100%;
        /* Full width for cards */
        margin-bottom: 10px;
        /* Smaller margin */
    }
}

/* Medium Devices (Tablets) */
@media (max-width: 992px) {
    .projects-section {
        padding: 40px 20px;
        /* Adjust padding for smaller screens */
    }

    .projects-section h1 {
        font-size: 2.5rem;
        /* Smaller heading size */
    }

    .project-box {
        width: 45%;
        /* Two items per row on medium screens */
    }
}

/* Small Devices (Mobile Phones) */
@media (max-width: 768px) {
    .projects-section {
        padding: 30px 15px;
        /* Further reduce padding */
    }

    .projects-section h1 {
        font-size: 2rem;
        /* Smaller heading size */
    }

    .projects-container {
        justify-content: center;
        /* Center align on smaller screens */
    }

    .project-box {
        width: 90%;
        /* Full width for project boxes on mobile */
        margin-bottom: 20px;
        /* Space between project boxes */
    }
}

/* Extra Small Devices (Phones in Portrait Mode) */
@media (max-width: 576px) {
    .projects-section h1 {
        font-size: 1.8rem;
        /* Smaller heading for extra small screens */
    }

    .project-box h3 {
        font-size: 1.3em;
        /* Smaller font size for project titles */
    }
}

/* Responsive Adjustments */

/* Medium Devices (Tablets) */
@media (max-width: 992px) {
    .contact-section {
        max-width: 90%;
        /* Increase width on medium devices */
        padding: 20px;
        /* Reduce padding */
    }

    .contact-section h1 {
        font-size: 1.8rem;
        /* Smaller heading size */
    }

    .contact-section p {
        font-size: 0.9rem;
        /* Smaller paragraph size */
    }

    .contact-form button {
        font-size: 0.8rem;
        /* Smaller button font size */
    }
}

/* Small Devices (Mobile Phones) */
@media (max-width: 768px) {
    .contact-section {
        max-width: 95%;
        /* Further increase width on small devices */
        padding: 15px;
        /* Reduce padding */
    }

    .contact-section h1 {
        font-size: 1.6rem;
        /* Smaller heading for mobile */
    }

    .contact-section p {
        font-size: 0.85rem;
        /* Smaller paragraph for mobile */
    }

    .contact-form input,
    .contact-form textarea {
        width: 100%;
        /* Full width for mobile inputs */
    }
}

/* Extra Small Devices (Phones in Portrait Mode) */
@media (max-width: 576px) {
    .contact-section h1 {
        font-size: 1.4rem;
        /* Even smaller heading */
    }

    .contact-form button {
        font-size: 0.75rem;
        /* Smaller button font for extra small screens */
    }
}

/* Contact Style End */